<template>
  <div class="article-editor">
    <ConfirmCard
      v-show="showConfirm"
      :form="form"
      :tags="tags"
      :filename="filenameFormat"
      @submit="submitForm('form')"
      @cancel="showConfirm = false"
    />
    <div class="mb2 f2 fw6 color-first editor-header">
      <span>
        
        
      </span>
      <el-button
        class="ml2"
        size="mini"
        type="primary"
        @click="showConfirm = true"
      ></el-button>
    </div>
    <el-form role="form" ref="form" :model="form" :rules="rules" status-icon>
      <el-row :gutter="20">
        <el-col :sm="12">
          <el-form-item prop="title">
            <el-input
              size="small"
              v-model="form.title"
              placeholder=""
              aria-placeholder=""
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :sm="12">
          <el-form-item prop="filename">
            <el-input
              size="small"
              v-model="form.filename"
              placeholder=""
              aria-placeholder=""
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :sm="12">
          <el-form-item prop="category">
            <el-input
              size="small"
              v-model="form.category"
              placeholder=""
              aria-placeholder=""
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :sm="12">
          <el-form-item prop="img">
            <el-input
              size="small"
              v-model="form.img"
              placeholder=""
              aria-placeholder=""
            ></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-form-item prop="brief">
        <el-input
          v-model="form.brief"
          type="textarea"
          placeholder=""
          aria-placeholder=""
        ></el-input>
      </el-form-item>
      <TagEditor
        :tags="tags"
        @add-tag="addTag"
        @close-tag="closeTag"
      ></TagEditor>
      <el-tabs type="border-card">
        <el-tab-pane label="">
          <el-form-item prop="content">
            <el-input
              v-model="form.content"
              type="textarea"
              placeholder=""
              aria-placeholder=""
              :rows="30"
            ></el-input>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="">
          <div class="markdown-body" v-html="markedHtml">lalal</div>
        </el-tab-pane>
      </el-tabs>
    </el-form>
  </div>
</template>

<script>
import FileSaver from 'file-saver'
import { toMarked, getSafeContent } from '../../../utils/article'
import TagEditor from './tag_editor'
import ConfirmCard from './confirm_card'
import path from 'path'

export default {
  name: 'ArticleEditor',

  data() {
    const validateFilename = (rule, value, callback) => {
      if (value.trim() === '') {
        callback(new Error(''))
      } else if (this.usedFilenames.includes(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    const validateString = (message) => {
      return (rule, value, callback) => {
        if (value.trim() === '') {
          callback(new Error(message))
        } else {
          callback()
        }
      }
    }
    return {
      showConfirm: false,
      form: {
        title: '',
        filename: '',
        category: '',
        img: '',
        brief: '',
        content: '',
      },
      tags: [],
      rules: {
        title: [
          {
            validator: validateString(''),
            trigger: 'blur',
          },
        ],
        filename: [
          {
            validator: validateFilename,
            trigger: 'blur',
          },
        ],
        category: [
          {
            validator: validateString(''),
            trigger: 'blur',
          },
        ],
        brief: [
          {
            validator: validateString(''),
            trigger: 'blur',
          },
        ],
      },
    }
  },

  computed: {
    markedHtml() {
      let safe = getSafeContent(this.form.content)
      return toMarked(safe)
    },

    filenameFormat() {
      let fn = this.form.filename
      return fn.trim().replace(/\s+/g, '-').toLowerCase()
    },
  },

  methods: {
    addTag(tag) {
      if (this.tags.includes(tag)) {
        this.$message({
          type: 'warn',
          message: `${tag}`,
        })
      } else {
        this.tags.push(tag)
      }
    },

    closeTag(tag) {
      let index = this.tags.indexOf(tag)
      this.tags.splice(index, 1)
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.generateFile()
          this.$message({
            type: 'info',
            message: '',
          })
          this.resetForm()
        } else {
          this.$message({
            type: 'error',
            message: '',
          })
        }
        this.showConfirm = false
      })
    },

    resetForm() {
      for (let key in this.form) {
        this.form[key] = ''
      }
      this.tags = []
    },

    generateFile() {
      let { content } = this.form
      let filename = this.filenameFormat
      let configInfo = this.generateConfig()
      this.saveFile(configInfo, filename + '.json')

      if (content.length !== 0) {
        this.saveFile(content, filename + '.md')
      }
    },

    generateTime() {
      let time = new Date()
      let year = time.getFullYear()
      let month = time.getMonth() + 1
      let day = time.getDate()
      return { year, month, day }
    },

    generateConfig() {
      let time = this.generateTime()
      let { img, title, brief, category } = this.form
      let tags = this.tags
      let filename = this.filenameFormat
      let filePath = this.generateFilePath(filename, time)

      let config = {
        filename,
        img: img.trim(),
        title: title.trim(),
        brief: brief.trim(),
        editedAt: Object.values(time).join('-'),
        category: category.trim(),
        tags,
        path: filePath,
      }
      return JSON.stringify(config)
    },

    generateFilePath(filename, time) {
      let {
        articleBasePath,
      } = this.getGlobalConfig()
      let archiveDate = time.year + '/' + time.month
      let filePath = path.join(
        articleBasePath,
        archiveDate,
        filename + '.md'
      )
      return filePath
    },

    saveFile(text, filename) {
      let file = new File([text], filename, {
        type: 'text/plain',
      })
      FileSaver.saveAs(file)
    },
  },

  components: {
    TagEditor,
    ConfirmCard,
  },

  mounted() {
    let database = this.getArticleDatabase()
    this.usedFilenames = database.filenames
  },
}
</script>

<style lang="scss" scoped>
.article-editor {
  border: 2px dashed #ddd;
  border-radius: .5rem;
  padding: 1rem;
  min-height: 25pc;

  textarea {
    font-family: $font-main;
  }

  .editor-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.color-first {
  color: $color-first;
}

.markdown-body {
  min-height: 10pc;
}
</style>



